"use client";

import Link from "next/link";

const menuLinks = [
  { label: "Admin", path: "/editor" },
  { label: "Cidade", path: "/city" },
  { label: "Casa", path: "/house" },
  { label: "Fazenda", path: "/farm" },
  { label: "Floresta", path: "/forest" },
  { label: "Lojas", path: "/shops" },
  { label: "Chat", path: "/chat" }
] as const;

export function BottomMenu() {
  return (
    <nav className="bottom-menu">
      {menuLinks.map((link) => (
        <Link key={link.path} href={link.path}>
          {link.label}
        </Link>
      ))}
    </nav>
  );
}
